<template>
  <div class="boxing">
    <h4>理赔申请单信息</h4>
    <el-form ref="form" :model="form" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="申请单号">
            <el-input v-model="form.claimNum"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="保单编号">
            <el-input v-model="form.policyOrder.policyNum"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="申请人">
            <el-input v-model="form.member.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="申请人身份证号">
            <el-input v-model="form.member.identify"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="理赔方式">
            <el-input v-model="form.payStyle"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="理赔项目">
            <el-input v-model="form.project"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="申请时间">
            <el-date-picker
              v-model="form.applicateTime"
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="打款时间">
            <el-date-picker
              v-model="form.payTime"
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="状态">
            <el-input v-model="form.status"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item>
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="cancel">取消</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        claimNum: "",
        policyOrder : [
          {
            policyNum:""
          }
        ],
        member: [
          {
            mid: "",
            name: ""
          }
        ]
      }
    };
  },
  created() {
    var that = this;
    if (this.$route.query.id) {
      const id = this.$route.query.id;
      that.$axios
        .get("/claimapplications/" + id//, {
          // headers: { Authorization: sessionStorage.getItem("auth") }
          //}
        )
        .then(function(res) {
          if (res.status == 200) {
            console.log(res.data.data);
            that.form = res.data.data;
          } else {

          }
        })
        .catch(function(error) {
          console.log(error);
        });
    } else {
    }
  },
  methods: {
    save() {
      var that = this;
      var claimNum = that.form.claimNum;
      var policyOrder = that.form.policyOrder;
      var member = that.form.member;
      var payStyle = that.form.payStyle;
      var applicateTime = that.form.applicateTime
      var payTime = that.form.payTime;
      var status = that.form.status;
      var project = that.form.project;
      if (this.$route.query.id) {
        // 修改
        this.$axios
          .put(
            "/claimapplications",
            {
              id: this.$route.query.id,
              claimNum: claimNum,
              payStyle: payStyle,
              applicateTime: applicateTime,
              payTime: payTime,
              status: status,
              project: project,
              member: member,
              policyOrder: policyOrder
            }//,
            // {
            //   headers: {
            //     Authorization: sessionStorage.getItem("auth")
            //   }
            // }
          )
          .then(function(res) {
            console.log(res);
            if (res.status == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.$router.push("/claim/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        // 添加
        this.$axios
          .post(
            "/claimapplications",
            {
              configId: configId,
              deviceGroupId: deviceGroupId,
              groupDescribe: groupDescribe,
              groupName: groupName
            }//,
            // {
            //   headers: {
            //     Authorization: sessionStorage.getItem("auth")
            //   }
            // }
          )
          .then(function(res) {
            console.log(res);
            if (res.status == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.$router.push("/claim/list");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    cancel(){
      this.$router.push("/claim/list");
    }
  }
};
</script>

<style>
.boxing {
  padding: 0 50px;
  height: 500px;
  /* background: red; */
  margin: 0 auto;
}
h4 {
  text-align: center;
  line-height: 80px;
}
</style>



